/* Dropdown.css */
$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.dropdown {
    height: 100%;
    width: fit-content;
    position: relative;
    display: inline-block;
  }
  
  .dropdown-toggle {
    padding: 4px 8px;
    cursor: pointer;
    color: #525B6C;
    font-size: 0.65rem;
    border-radius: 3px;
    background-color: $lighter_shade;
    border: none;
    border: 1.5px solid #DFE5F9 ;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
   
  }
  
  .dropdown-menu {
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    border: 1px solid #ccc;
    z-index: 1;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .dropdown-item {
    padding: 8px 16px;
    cursor: pointer;
    font-size: 0.65rem;
    color: #525B6C;

  }
  
  .dropdown-item:hover {
    background-color: $background_shade;

  }
  