.testcase-box{
    width: 100%;
    height: 100%;
  
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
   
    h4{ 
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: 15%;
        color: #525B6C;
        font-size: 0.8rem;
        font-weight: 600;
        padding: 14px;
        border-bottom: 1.5px solid #DFE5F9 ;
    }
    .box-list{
        width: 100%;
        height: 85%;
        overflow-y: scroll;
        .check-box{
            border-bottom: 1.5px solid #DFE5F9 ;
            width: 100%;
            padding: 11px 15px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            gap: 10px;
            label{
                font-size: 0.65rem;
                font-weight: 600;
                color: #7282A3;
                cursor: pointer;
                
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;

                span{
                    display: inline-block;
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                }

            }
            input{
                cursor: pointer;
                
            }
        }
        
    }

    ::-webkit-scrollbar {
        width: 5px;
      }
      
      /* Track */
      ::-webkit-scrollbar-track {
        background: #FAFBFF; 
      }
       
      /* Handle */
      ::-webkit-scrollbar-thumb {
        background: #E7ECFB; 
      }
    
}